"use client"

import { useState } from "react"
import Link from "next/link"
import {
  Bot,
  ChevronDown,
  Home,
  Wrench,
  BookOpen,
  Database,
  BarChart3,
  FileText,
  Activity,
  Users,
  CreditCard,
  Link2,
  Bell,
  Settings,
  Check,
} from "lucide-react"
import { cn } from "@/lib/utils"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"

const agents = [
  { id: 1, name: "BKLite Cloud 助手", version: "v2.4", active: true },
  { id: 2, name: "ITSM Expert", version: "v1.2", active: true },
  { id: 3, name: "Workflow Engine", version: "v3.1", active: false },
  { id: 4, name: "HR Assistant", version: "v1.0", active: true },
]

const menuItems = [
  { icon: Home, label: "Workspace", href: "/", active: true },
  { icon: Wrench, label: "ITSM", href: "/service-catalog", hasSubmenu: true },
  { icon: BookOpen, label: "Knowledge Base", href: "#" },
  { icon: Database, label: "CMDB", href: "#" },
  { icon: Bell, label: "Events", href: "#" },
]

const analyticsItems = [
  { icon: BarChart3, label: "Metrics", href: "#" },
  { icon: FileText, label: "Logs", href: "#" },
  { icon: Activity, label: "Performance", href: "#" },
]

const settingsItems = [
  { icon: Users, label: "Organization", href: "#", hasSubmenu: true },
  { icon: CreditCard, label: "Billing", href: "#", hasSubmenu: true },
  { icon: Link2, label: "Integrations", href: "#", hasSubmenu: true },
]

interface GlobalSidebarProps {
  currentPage?: string
}

export function GlobalSidebar({ currentPage = "workspace" }: GlobalSidebarProps) {
  const [selectedAgent, setSelectedAgent] = useState(agents[0])
  const [agentPopoverOpen, setAgentPopoverOpen] = useState(false)

  return (
    <aside className="w-[260px] h-screen bg-white border-r border-gray-100 flex flex-col fixed left-0 top-0 z-40">
      {/* Logo Header */}
      <div className="h-16 px-5 flex items-center gap-3 border-b border-gray-50">
        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary to-indigo-400 flex items-center justify-center">
          <Bot className="w-5 h-5 text-white" />
        </div>
        <span className="font-semibold text-foreground tracking-tight">BKLite Cloud</span>
      </div>

      {/* Agent Switcher */}
      <div className="px-4 py-4">
        <Popover open={agentPopoverOpen} onOpenChange={setAgentPopoverOpen}>
          <PopoverTrigger asChild>
            <button
              type="button"
              className="w-full flex items-center gap-3 px-3 py-2.5 rounded-xl bg-gradient-to-r from-indigo-50 to-purple-50 border border-indigo-100/50 hover:shadow-md transition-all duration-200"
            >
              <div className="w-9 h-9 rounded-lg bg-gradient-to-br from-primary to-violet-500 flex items-center justify-center shadow-sm">
                <Bot className="w-5 h-5 text-white" />
              </div>
              <div className="flex-1 text-left">
                <p className="text-sm font-medium text-foreground truncate">{selectedAgent.name}</p>
                <p className="text-xs text-muted-foreground">{selectedAgent.version}</p>
              </div>
              <ChevronDown className="w-4 h-4 text-muted-foreground" />
            </button>
          </PopoverTrigger>
          <PopoverContent className="w-[232px] p-0" align="start">
            <div className="p-3 border-b border-gray-100">
              <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide">Select Agent Context</p>
            </div>
            <div className="p-2">
              {agents.map((agent) => (
                <button
                  key={agent.id}
                  type="button"
                  onClick={() => {
                    setSelectedAgent(agent)
                    setAgentPopoverOpen(false)
                  }}
                  className={cn(
                    "w-full flex items-center gap-3 px-3 py-2.5 rounded-lg transition-colors",
                    selectedAgent.id === agent.id
                      ? "bg-indigo-50 text-primary"
                      : "hover:bg-gray-50 text-foreground"
                  )}
                >
                  <div className={cn(
                    "w-8 h-8 rounded-lg flex items-center justify-center",
                    selectedAgent.id === agent.id
                      ? "bg-gradient-to-br from-primary to-violet-500"
                      : "bg-gray-100"
                  )}>
                    <Bot className={cn("w-4 h-4", selectedAgent.id === agent.id ? "text-white" : "text-gray-500")} />
                  </div>
                  <div className="flex-1 text-left">
                    <p className="text-sm font-medium truncate">{agent.name}</p>
                    <div className="flex items-center gap-2">
                      <span className="text-xs text-muted-foreground">{agent.version}</span>
                      {agent.active && (
                        <span className="flex items-center gap-1">
                          <span className="w-1.5 h-1.5 rounded-full bg-emerald-500 animate-pulse-slow" />
                          <span className="text-xs text-emerald-600">Active</span>
                        </span>
                      )}
                    </div>
                  </div>
                  {selectedAgent.id === agent.id && (
                    <Check className="w-4 h-4 text-primary" />
                  )}
                </button>
              ))}
            </div>
            <div className="p-2 border-t border-gray-100">
              <button
                type="button"
                className="w-full flex items-center justify-center gap-2 px-3 py-2 rounded-lg text-sm font-medium text-primary hover:bg-indigo-50 transition-colors"
              >
                <Settings className="w-4 h-4" />
                Manage Agents
              </button>
            </div>
          </PopoverContent>
        </Popover>
      </div>

      {/* Navigation Menu */}
      <nav className="flex-1 px-3 overflow-y-auto">
        <div className="space-y-1">
          {menuItems.map((item) => {
            const isActive = currentPage === item.label.toLowerCase().replace(" ", "-")
            return (
              <Link
                key={item.label}
                href={item.href}
                className={cn(
                  "flex items-center gap-3 px-3 py-2.5 rounded-xl text-sm font-medium transition-all duration-200 relative",
                  isActive
                    ? "bg-indigo-50 text-primary"
                    : "text-gray-500 hover:text-foreground hover:bg-gray-50"
                )}
              >
                {isActive && (
                  <span className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-5 bg-primary rounded-r-full" />
                )}
                <item.icon className="w-5 h-5" />
                <span>{item.label}</span>
                {item.hasSubmenu && (
                  <ChevronDown className="w-4 h-4 ml-auto" />
                )}
              </Link>
            )
          })}
        </div>

        {/* Analytics Section */}
        <div className="mt-6 pt-6 border-t border-gray-100">
          <p className="px-3 text-xs font-medium text-muted-foreground uppercase tracking-wide mb-2">Analytics</p>
          <div className="space-y-1">
            {analyticsItems.map((item) => (
              <Link
                key={item.label}
                href={item.href}
                className="flex items-center gap-3 px-3 py-2.5 rounded-xl text-sm font-medium text-gray-500 hover:text-foreground hover:bg-gray-50 transition-colors"
              >
                <item.icon className="w-5 h-5" />
                <span>{item.label}</span>
              </Link>
            ))}
          </div>
        </div>

        {/* Settings Section */}
        <div className="mt-6 pt-6 border-t border-gray-100">
          <p className="px-3 text-xs font-medium text-muted-foreground uppercase tracking-wide mb-2">Settings</p>
          <div className="space-y-1">
            {settingsItems.map((item) => (
              <Link
                key={item.label}
                href={item.href}
                className="flex items-center gap-3 px-3 py-2.5 rounded-xl text-sm font-medium text-gray-500 hover:text-foreground hover:bg-gray-50 transition-colors"
              >
                <item.icon className="w-5 h-5" />
                <span>{item.label}</span>
                {item.hasSubmenu && (
                  <ChevronDown className="w-4 h-4 ml-auto" />
                )}
              </Link>
            ))}
          </div>
        </div>
      </nav>

      {/* User Profile */}
      <div className="p-4 border-t border-gray-100">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary to-violet-500 flex items-center justify-center text-white font-semibold">
            X
          </div>
          <div className="flex-1 min-w-0">
            <p className="text-sm font-medium text-foreground truncate">Xiaoyu Liu</p>
            <p className="text-xs text-muted-foreground truncate">umaru workspace</p>
          </div>
        </div>
      </div>
    </aside>
  )
}
